import {Button, Card, CloseButton, Form} from "react-bootstrap";
import {motion} from "framer-motion";

const spring = {
    type: 'spring',
    damping: 25,
    stiffness: 120,
    duration: 0.25,
};

export const TodoItemsList = function (props) {



    const state = props.state
    const setState = props.setState

    const sortedItems = state.todoItems.slice().sort((a, b) => {
        if (a.done && !b.done) {
            return 1;
        }

        if (!a.done && b.done) {
            return -1;
        }

        return 0;
    });


    return (
        <ul>
            {sortedItems.map((item) => (
                <motion.li key={item.id} transition={spring} layout={true}>
                    <TodoItemCard state={state} setState={setState} item={item}/>
                </motion.li>
            ))}
        </ul>
    );
};


export const TodoItemCard = function (props) {

    const item = props.item
    const state = props.state
    const setState = props.setState

    const handleDelete = () => {
        let a ={
            todoItems: state.todoItems.filter(
                ({id}) => id !== item.id,
            )
        }
        setState(a)
    }
    const handleToggleDone = () => {
        const itemIndex = state.todoItems.findIndex(
            ({ id }) => id === item.id,
        );
        const itemq = state.todoItems[itemIndex];

        let a ={ todoItems: [
            ...state.todoItems.slice(0, itemIndex),
            { ...item, done: !itemq.done },
            ...state.todoItems.slice(itemIndex + 1),
        ],}
        setState(a)

    }


    return (
        <Card bg='light'>
            <Card.Header className='todo-header'>{item.title}
                <CloseButton className='align-self-end' onClick={handleDelete}/>

            </Card.Header>
            <Form.Label
                placeholder={item.title} >{item.title}</Form.Label>
            {item.details ? (
                    <Card.Body>
                        <Card.Text variant="body2" component="p">
                            {item.details}
                        </Card.Text>
                    </Card.Body>
                ) : null}
            <Form.Check aria-label="option 1" onChange={handleToggleDone} />
        </Card>
    );
}

